import './Header.css';
import { NavLink } from 'react-router-dom';
import LogoutButton from '../LogoutButton/LogoutButton';
import { AiOutlineSearch } from 'react-icons/ai';
import { IoAlbums } from 'react-icons/io5';

const Header = ({ setGToken, DPSrc }) => {
	return (
		<header>
			<div className='logo'>
				<div className='logo__img-cntr'>
					<img src='/assets/logo192.png' alt='icon' className='logo__img' />
				</div>
				<span className='logo__title'>JamPodd</span>
			</div>

			<nav className='nav'>
				<ul>
					<li>
						<NavLink exact to='/explore' activeClassName='--active'>
							<div className='nav__icon-cntr'>
								<AiOutlineSearch />
							</div>
							<span>Explore</span>
						</NavLink>
					</li>
					<li>
						<NavLink exact to='/playlists' activeClassName='--active'>
							<div className='nav__icon-cntr'>
								<IoAlbums />
							</div>
							<span>Playlists</span>
						</NavLink>
					</li>
				</ul>
			</nav>

			<div className='logout'>
				<LogoutButton setGToken={setGToken} DPSrc={DPSrc} />
			</div>
		</header>
	);
};

export default Header;
